    <h2>Després de confinaments, moments difícils i molta paciència, em vaig graduar en Belles Arts amb un projecte d'un videoclip animat en procés. Es tracta de Fivetowers, una cançó de Ceci Dolç que, junt amb la meua parella, estem animant. Encara que no l’hem arribat a acabar, l’experiència de treballar en aquesta animació és el que m’ha permés trobar la feina en la qual estic treballant actualment. Sóc nova en això de la indústria de l’animació, però trobo que és un món preciós i ple d’oportunitats, hi ha tants llenguatges diferents dins d’aquesta tècnica que mai deixa de sorprendre’t.</h2>
    
