    <h2>Tots aquests són grans assoliments per a la meua carrera professional, però el que realment em fa seguir volent treballar com a artista, il·lustradora o animadora són els dibuixos més personals, esbossos que tinc guardats al quadern i que la gent sol passar per alt. Potser no són els més impressionants o els més elaborats, però sí són els més sincers. Moltes vegades hi ha sentiments als quals no sé posar nom ni origen, però sí textura i color, i encara que no els pugui entendre, el dibuix em permet soltar-los i compartir-los amb la gent.</h2>
    
